.contacts {
    background-color: #191825;
    text-align: center;
}

.contact-head,
.footer {
    color: yellow;

}

.footer {
    font-size: 1.5rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.contact-button {
    display: inline;
    margin: 10px;
}
.rtr {
    border: none;
    color: yellow;
    text-align: center;
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    text-decoration: none;
    display: inline-block;
    font-size: 20px;
    margin: 4px 2px;
    cursor: pointer;
}